@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Quicksand', sans-serif}.app-wrapper{display:grid;grid:"header header" 3.5em "nav content" calc(100vh - 3.5em);grid-template-columns:13% 87%;justify-content:center}.header{display:flex;flex-direction:row;align-items:center;grid-area:header;background-color:#eaedee;color:#020202;font-size:1.2em;align-self:center;align-content:center}.header img{margin-left:1.1em;width:2.4em}.header p{padding-left:1em;font-weight:600}.nav{grid-area:nav;background-color:#042135;color:#fff;font-size:1.1em;display:flex;flex-direction:column}.nav a:first-child{margin-top:1.4em}.nav a{margin-left:1.2em;margin-bottom:1.6em;border-bottom:1px solid white;padding-bottom:0.3em;width:6em;cursor:pointer}.nav a:hover{color:#008cff;border-color:#5574ff}.content{grid-area:content;background-color:#f0efeb;color:#000;font-size:1em;padding:2em}

